import React, { Component, Fragment } from "react";
import axios from "axios";
import ProductosEcommerce from './ProductosEcommerce'

export default class RubrosEcommerce extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      search: '',
      value: '',
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  receivedRubro() {
    axios.get(`https://www.sundelimpieza.com/api/rubros/all`).then((res) => {
      const data = res.data;
      const postRubro = data.map((pd) => (
        <React.Fragment>
          <div>
              <div class="uk-card uk-card-default">
                  <div class="uk-card-media-top">
                    <img
                      src={"https://www.sundelimpieza.com/static/images/" + pd.Foto}
                      alt="Producto SundeLimpieza"
                      className="uk-max-width uk-padding-small"
                    />
                  </div>
                  <div class="uk-card-body">
                    <h3 class="uk-card-title">{pd.SubRubro}</h3>
                    <button class="uk-button uk-button-primary" onClick={this.handleClick} id={pd.SubRubro}>Ver Productos</button>
                  </div>
              </div>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubro,
      });
    });
  }

  handleClick(e) {
    this.setState({ value: e.target.getAttribute('id') });
  }

  handleChange(e) {
    this.setState({ search: e.target.value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({ value: this.state.search });
  };

  componentDidMount() {
    this.receivedRubro();
  }

  render() {
    return (
      <div className="uk-container uk-margin uk-padding-bottom">
          

            {
              this.state.value ?
                <ProductosEcommerce SubRubro={this.state.value}/>   
              : 
                <Fragment>
                <div className="uk-grid-small uk-child-width-expand@s" uk-grid="true">
                  <div>
                    <h2 className="uk-text-bolder uk-text-center">¿ Que Productos estas buscando ?</h2>
                  </div>
                </div>
                <div class="uk-child-width-1-1 uk-text-center" uk-grid="true">
                  <div>
                    <form class="uk-search uk-search-large" onSubmit={this.handleSubmit}>
                        <input class="uk-search-input" type="search" placeholder="..Ingresar Busqueda...." onChange={this.handleChange}/>
                    </form>
                  </div>
                </div>
                <h2 className="uk-text-bolder uk-text-center">Selecciona el rubro que mas te interese</h2>
                <div className="uk-text-center uk-grid-column-small uk-grid-row-small uk-grid-divider uk-child-width-1-4@s uk-text-center uk-grid-match" uk-grid="true">
                  {this.state.postRubro}
                </div>
                </Fragment>
            }
        
      </div>
    );
  }
}
