import React, { Component, Fragment } from "react";
import axios from "axios";
import {Link} from 'react-router-dom'
import ProductosEcommerceMenu from './ProductosEcommerceMenu'
import ProductoDetail from './ProductoDetail'

export default class RubrosEcommerce extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      search: '',
      link: this.props.Menu,
      value: this.props.SubRubro,
      detail: '',
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  receivedProducto() {
    axios.get(`https://www.sundelimpieza.com/api/productos/producto/${this.state.value}`).then((res) => {
      const data = res.data;
      const postProducto = data.map((pd) => (
        <React.Fragment>
            <div>
              <div className="uk-card uk-card-default uk-card-small uk-card-body uk-text-center">
                <img
                  src={"https://www.sundelimpieza.com/static/images/" + pd._id.Foto}
                  alt="Producto SundeLimpieza"
                  className="uk-max-width"
                />
                <h3 className="uk-card-title">{pd._id.Producto}</h3>
                <Link to="#" onClick={this.handleClickDetail}>
                  <button class="uk-button uk-button-primary" id={pd._id.Producto}>Ver Detalle</button>
                </Link>
              </div>
            </div>
        </React.Fragment>
      ));

      this.setState({
        postProducto,
      });
    });
  }

  receivedRubro() {
    axios.get(`https://www.sundelimpieza.com/api/productos/subrubro`).then((res) => {
      const data = res.data;
      const postRubro = data.map((pd) => (
        <React.Fragment>
          <div className="uk-padding-list">
            <Link to="#" onClick={this.handleClick}>
              <li className="uk-text-capitalize uk-text-break uk-text-small" id={pd._id.SubRubro}>{pd._id.SubRubro}</li>
            </Link>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubro,
      });
    });
  }

  componentDidMount() {
    this.receivedProducto();
    this.receivedRubro();
  }

  handleClick(e) {
    this.setState({ link: e.target.getAttribute('id') });
  }

  handleChange(e) {
    this.setState({ search: e.target.value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({ link: this.state.search });
  };

  handleClickDetail = (e) => {
    this.setState({ detail: e.target.getAttribute('id') });
  }

  render() {
    return (
      <div className="uk-container">
        
          {
              this.state.link ?
                  <ProductosEcommerceMenu Menu={this.state.link} />
              :
                this.state.detail ?
                  <ProductoDetail Producto={this.state.detail} />
                :
                <Fragment>
                  <div className="uk-grid-small uk-child-width-expand@s" uk-grid="true">
                    <div>
                      <h2 className="uk-text-bolder uk-text-center">¿ Que Productos estas buscando ?</h2>
                    </div>
                  </div>
                  <div class="uk-child-width-1-1 uk-text-center" uk-grid="true">
                    <div>
                      <form class="uk-search uk-search-large" onSubmit={this.handleSubmit}>
                        <input class="uk-search-input" type="search" placeholder="..Ingresar Busqueda...." onChange={this.handleChange}/>
                      </form>
                    </div>
                  </div>
                  
                  <hr class="uk-divider-icon"></hr>

                  <div className="uk-text-center" uk-grid="true">
                      <div className="uk-width-expand@m">
                        <h4 className="uk-text-bolder uk-text-left uk-margin-top">Listado de Productos:</h4>
                        <div className="uk-child-width-1-2@s uk-child-width-1-3@m uk-grid-small" uk-grid="masonry: true">
                          {this.state.postProducto}
                        </div>
                      </div>
                      <div className="uk-width-1-4@m">
                          <div className="uk-card uk-card-default uk-card-body">
                            <h4 className="uk-text-bolder uk-text-left uk-margin-top">Listado de Rubros:</h4>
                            <ul className="uk-list uk-text-left uk-list-large">
                              {this.state.postRubro}
                            </ul>
                          </div>
                      </div>
                  </div>
                </Fragment>
          }
        
      </div>
    );
  }
}
